import Vue from 'vue'
import Router from 'vue-router'

const Main = (resolve) => {
  import('@/main/main').then((module) => {
    resolve(module)
  })
}
// 首页
const Home = (resolve) => {
  import('components/home/home').then((module) => {
    resolve(module)
  })
}

const HomeSystem = (resolve) => {
  import('components/home/system/system').then((module) => {
    resolve(module)
  })
}
// 预约挂号
const Bespeak = (resolve) => {
  import('components/bespeak/bespeak').then((module) => {
    resolve(module)
  })
}

const BespeakManagement = (resolve) => {
  import('components/bespeak/bespeakManagement/bespeakManagement').then((module) => {
    resolve(module)
  })
}

const SchedulingManagement = (resolve) => {
  import('components/bespeak/schedulingManagement/schedulingManagement').then((module) => {
    resolve(module)
  })
}
// 会员
const Members = (resolve) => {
  import('components/members/members').then((module) => {
    resolve(module)
  })
}

const MemberList = (resolve) => {
  import('components/members/memberList/memberList').then((module) => {
    resolve(module)
  })
}

const MemberScore = (resolve) => {
  import('components/members/memberScore/memberScore').then((module) => {
    resolve(module)
  })
}
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home/system',
      component: Main,
      children: [
        {
          path: 'home',
          redirect: '/home/system',
          component: Home,
          children: [
            {
              path: 'system',
              component: HomeSystem
            }
          ]
        },
        {
          path: 'bespeak',
          redirect: '/bespeak/bespeakManagement',
          component: Bespeak,
          children: [
            {
              path: 'bespeakManagement',
              component: BespeakManagement
            },
            {
              path: 'schedulingManagement',
              component: SchedulingManagement
            }
          ]
        },
        {
          path: 'members',
          redirect: '/members/memberList',
          component: Members,
          children: [
            {
              path: 'memberList',
              component: MemberList
            },
            {
              path: 'memberScore',
              component: MemberScore
            }
          ]
        }
      ]
    }
  ]
})



